'use client';

import { useState } from 'react';
import { FactoryCase } from '@/types';

interface CaseCardProps {
  case: FactoryCase;
  onStatusUpdate: (caseId: string, status: string) => void;
  onCommentAdd: (caseId: string, comment: string) => void;
}

export default function CaseCard({ case: caseItem, onStatusUpdate, onCommentAdd }: CaseCardProps) {
  const [showCommentForm, setShowCommentForm] = useState(false);
  const [comment, setComment] = useState('');
  const [isUpdating, setIsUpdating] = useState(false);

  const handleStatusChange = async (e: React.ChangeEvent<HTMLSelectElement>) => {
    const newStatus = e.target.value;
    setIsUpdating(true);
    try {
      await onStatusUpdate(caseItem.id, newStatus);
    } finally {
      setIsUpdating(false);
    }
  };

  const handleCommentSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!comment.trim()) return;
    
    setIsUpdating(true);
    try {
      await onCommentAdd(caseItem.id, comment);
      setComment('');
      setShowCommentForm(false);
    } finally {
      setIsUpdating(false);
    }
  };

  const getStatusColor = (status: string) => {
    // Êó•Êú¨Ë™û„Çπ„ÉÜ„Éº„Çø„Çπ„Å®Ëã±Ë™û„Çπ„ÉÜ„Éº„Çø„Çπ„ÅÆ‰∏°Êñπ„Å´ÂØæÂøú
    const statusLower = status.toLowerCase();
    if (status === 'Âèó‰ªò' || statusLower === 'pending') {
      return 'bg-yellow-100 text-yellow-800';
    }
    if (status === 'Ë®∫Êñ≠‰∏≠' || status === '‰øÆÁêÜ‰∏≠' || statusLower === 'in_progress') {
      return 'bg-blue-100 text-blue-800';
    }
    if (status === 'ÂÆå‰∫Ü' || statusLower === 'completed') {
      return 'bg-green-100 text-green-800';
    }
    if (status === '„Ç≠„É£„É≥„Çª„É´' || statusLower === 'cancelled') {
      return 'bg-red-100 text-red-800';
    }
    return 'bg-gray-100 text-gray-800';
  };

  const getStatusLabel = (status: string) => {
    // Êó¢Â≠ò„ÅÆ„Çπ„ÉÜ„Éº„Çø„Çπ„Çí„Åù„ÅÆ„Åæ„ÅæËøî„ÅôÔºàÊó•Êú¨Ë™û„ÅÆÂèØËÉΩÊÄß„Åå„ÅÇ„ÇãÔºâ
    return status;
  };

  return (
    <div className="bg-white border border-gray-200 rounded-lg p-6 shadow-sm hover:shadow-md transition-shadow">
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900 mb-2">
            {caseItem.title || `Ê°à‰ª∂ #${caseItem.id}`}
          </h3>
          {caseItem.customerName && (
            <p className="text-sm text-gray-600 mb-1">
              È°ßÂÆ¢Âêç: {caseItem.customerName}
            </p>
          )}
          {caseItem.description && (
            <p className="text-sm text-gray-600 mb-2 line-clamp-2">
              {caseItem.description}
            </p>
          )}
          {caseItem.user_message && (
            <div className="bg-blue-50 border-l-4 border-blue-400 p-3 rounded mb-2">
              <p className="text-xs font-semibold text-blue-800 mb-1">„É¶„Éº„Ç∂„Éº:</p>
              <p className="text-sm text-gray-700">
                {caseItem.user_message.length > 200
                  ? `${caseItem.user_message.substring(0, 200)}...`
                  : caseItem.user_message}
              </p>
            </div>
          )}
          {caseItem.bot_message && (
            <div className="bg-green-50 border-l-4 border-green-400 p-3 rounded mb-2">
              <p className="text-xs font-semibold text-green-800 mb-1">AIÂøúÁ≠î:</p>
              <p className="text-sm text-gray-700">
                {caseItem.bot_message.length > 200
                  ? `${caseItem.bot_message.substring(0, 200)}...`
                  : caseItem.bot_message}
              </p>
            </div>
          )}
          {caseItem.category && (
            <p className="text-xs text-gray-500 mb-1">
              üè∑Ô∏è {caseItem.category}
            </p>
          )}
        </div>
        <span className={`px-3 py-1 rounded-full text-xs font-semibold ${getStatusColor(caseItem.status)}`}>
          {getStatusLabel(caseItem.status)}
        </span>
      </div>

      <div className="grid grid-cols-2 gap-4 mb-4 text-sm text-gray-600">
        <div>
          <span className="font-semibold">‰ΩúÊàêÊó•:</span>{' '}
          {caseItem.timestamp || caseItem.created_time || caseItem.createdAt
            ? new Date(caseItem.timestamp || caseItem.created_time || caseItem.createdAt).toLocaleDateString('ja-JP')
            : 'N/A'}
        </div>
        <div>
          <span className="font-semibold">Êõ¥Êñ∞Êó•:</span>{' '}
          {caseItem.updatedAt
            ? new Date(caseItem.updatedAt).toLocaleDateString('ja-JP')
            : 'N/A'}
        </div>
      </div>
      {caseItem.comment && (
        <div className="bg-gray-50 p-3 rounded mb-4 text-sm text-gray-700 whitespace-pre-wrap">
          {caseItem.comment}
        </div>
      )}

      <div className="flex gap-2">
        <select
          value={caseItem.status}
          onChange={handleStatusChange}
          disabled={isUpdating}
          className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100"
        >
          <option value="Âèó‰ªò">Âèó‰ªò</option>
          <option value="Ë®∫Êñ≠‰∏≠">Ë®∫Êñ≠‰∏≠</option>
          <option value="‰øÆÁêÜ‰∏≠">‰øÆÁêÜ‰∏≠</option>
          <option value="ÂÆå‰∫Ü">ÂÆå‰∫Ü</option>
          <option value="„Ç≠„É£„É≥„Çª„É´">„Ç≠„É£„É≥„Çª„É´</option>
        </select>
        
        <button
          onClick={() => setShowCommentForm(!showCommentForm)}
          className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors"
        >
          üí¨ „Ç≥„É°„É≥„Éà
        </button>
      </div>

      {showCommentForm && (
        <form onSubmit={handleCommentSubmit} className="mt-4 pt-4 border-t border-gray-200">
          <textarea
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            placeholder="„Ç≥„É°„É≥„Éà„ÇíÂÖ•Âäõ..."
            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 mb-2"
            rows={3}
          />
          <div className="flex gap-2">
            <button
              type="submit"
              disabled={isUpdating || !comment.trim()}
              className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed"
            >
              ÈÄÅ‰ø°
            </button>
            <button
              type="button"
              onClick={() => {
                setShowCommentForm(false);
                setComment('');
              }}
              className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200"
            >
              „Ç≠„É£„É≥„Çª„É´
            </button>
          </div>
        </form>
      )}
    </div>
  );
}

