'use client';

import { useState, useEffect } from 'react';
import { FactoryCase } from '@/types';
import { factoryApi } from '@/lib/api';
import CaseList from '@/components/Factory/CaseList';
import StatusFilter from '@/components/Factory/StatusFilter';
import Navigation from '@/components/Navigation';

export default function FactoryDashboardPage() {
  const [cases, setCases] = useState<FactoryCase[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [activeStatus, setActiveStatus] = useState<string>('');

  const loadCases = async (status: string = '') => {
    setIsLoading(true);
    try {
      const fetchedCases = await factoryApi.getCases(status || undefined);
      setCases(fetchedCases);
    } catch (error: any) {
      console.error('Ê°à‰ª∂ÂèñÂæó„Ç®„É©„Éº:', error);
      const errorMessage = error?.response?.data?.error || error?.message || 'Ê°à‰ª∂„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü';
      console.error('„Ç®„É©„ÉºË©≥Á¥∞:', {
        status: error?.response?.status,
        data: error?.response?.data,
        message: error?.message,
      });
      alert(`Ê°à‰ª∂„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü: ${errorMessage}`);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadCases(activeStatus);
  }, [activeStatus]);

  const handleStatusUpdate = async (caseId: string, status: string) => {
    try {
      // page_id„Çí‰ΩøÁî®ÔºàÊó¢Â≠òAPI„Å®„ÅÆ‰∫íÊèõÊÄßÔºâ
      const caseItem = cases.find((c) => c.id === caseId || c.page_id === caseId);
      const pageId = caseItem?.page_id || caseId;
      
      if (!pageId) {
        alert('‚ùå Ê°à‰ª∂ID„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì');
        return;
      }
      
      await factoryApi.updateCaseStatus(pageId, status);
      alert('‚úÖ „Çπ„ÉÜ„Éº„Çø„Çπ„ÇíÊõ¥Êñ∞„Åó„Åæ„Åó„Åü');
      loadCases(activeStatus); // ÂÜçË™≠„ÅøËæº„Åø
    } catch (error) {
      console.error('„Çπ„ÉÜ„Éº„Çø„ÇπÊõ¥Êñ∞„Ç®„É©„Éº:', error);
      alert('‚ùå „Çπ„ÉÜ„Éº„Çø„Çπ„ÅÆÊõ¥Êñ∞„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
    }
  };

  const handleCommentAdd = async (caseId: string, comment: string) => {
    try {
      const caseItem = cases.find((c) => c.id === caseId || c.page_id === caseId);
      const pageId = caseItem?.page_id || caseId;
      
      if (!pageId) {
        alert('‚ùå Ê°à‰ª∂ID„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì');
        return;
      }
      
      await factoryApi.addComment(pageId, comment);
      alert('‚úÖ „Ç≥„É°„É≥„Éà„ÇíËøΩÂä†„Åó„Åæ„Åó„Åü');
      loadCases(activeStatus); // ÂÜçË™≠„ÅøËæº„Åø
    } catch (error) {
      console.error('„Ç≥„É°„É≥„ÉàËøΩÂä†„Ç®„É©„Éº:', error);
      alert('‚ùå „Ç≥„É°„É≥„Éà„ÅÆËøΩÂä†„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <div className="max-w-7xl mx-auto">
        {/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥ */}
        <Navigation />

        {/* „Éò„ÉÉ„ÉÄ„Éº */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <div className="flex justify-between items-center flex-wrap gap-4">
            <h1 className="text-3xl font-bold text-gray-900">Â∑•Â†¥Âêë„Åë„ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„Éâ</h1>
            <div className="flex gap-4 items-center">
              <span className="text-sm text-gray-600">
                Ê°à‰ª∂Êï∞: <span className="font-semibold">{cases.length}‰ª∂</span>
              </span>
            </div>
          </div>
        </div>

        {/* „Éï„Ç£„É´„Çø */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">„Çπ„ÉÜ„Éº„Çø„Çπ„Éï„Ç£„É´„Çø</h2>
          <StatusFilter activeStatus={activeStatus} onStatusChange={setActiveStatus} />
        </div>

        {/* Ê°à‰ª∂‰∏ÄË¶ß */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Ê°à‰ª∂‰∏ÄË¶ß</h2>
            <button
              onClick={() => loadCases(activeStatus)}
              className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
            >
              üîÑ Êõ¥Êñ∞
            </button>
          </div>
          <CaseList
            cases={cases}
            isLoading={isLoading}
            onStatusUpdate={handleStatusUpdate}
            onCommentAdd={handleCommentAdd}
          />
        </div>
      </div>
    </div>
  );
}

